.HeaderRow {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    background: linear-gradient(#504b45 0%,#3c3b37 100%);
    font-size: large;
    padding: 5px;
}

.AppThree {
    background-color: rgba(56, 4, 40);
    text-shadow: 0px 1px 0px rgba(255, 255, 255, 0.2);
    height: 100%;
}

.MainContainer {
    display: flex;
    flex-direction: row;
    color: #ddd;
}

.Tuser {

    color: #87d441;
  
  }
  
.Tlocation {
  
    color: #6d85a9;
  
  }

.Ttext {
    color: #ddd;
}

.ResourceColumn {
    display: flex;
    flex-direction: column;
    padding: 5px;
    width: 100px;
}

.NavColumn {
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.NavRow {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 5px;
}

.resourceColor {
 color: #3465A4
}

button {
    background: linear-gradient(#7d7871 0%, #595953 100%);  
    text-shadow: 0px 1px 0px rgba(255, 255, 255, 0.2);
    box-shadow: 0px 0px 1px 0px #41403a, 0px 1px 1px 0px #474642;
    border: none;
    color: #ddd;
}

.MainContainer button {
    height: 35px;
}

.badButton button {
    background: linear-gradient(#f37458 0%, #de4c12 100%);
}

.HeaderRow button {
    border-radius: 7px;
    height: 100%;
}

