@import '../../shared.scss';

.TipFC {
    display: none;
}

:hover > * > .TipFC {
    display: initial;
}

.TipFC {
    border: 2px solid $-textColor;
    color:$-textColor;
    z-index: 1;
    background-color: $-bgColor;

    position: absolute;
    white-space: nowrap;
    margin: 5px;
    padding: 5px;
    pointer-events: none;
    
}

.TipFC > * {
    display: flex;
    white-space: pre-line;
}